.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 16px;
}

.icon {
  width: 160px;
  height: 134px;
  margin: 0 auto 23px;
  padding-top: 16px;
  display: block;
}

.reviews {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.reviews__number {
  color: #060b35;
  text-align: center;
  padding-top: 2px;
  padding-left: 17px;
  font-weight: 400;
}

.btn {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.price {
  justify-content: space-between;
  width: 166px;
  height: 18px;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.price__name {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.price__value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.item {
  text-align: left;
  padding-top: 17px;
}

