  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  line-height: 18px;
}

.item__code {
  color: #616070;
  margin-bottom: 16px;
  font-weight: 400;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 500;
  line-height: 14px;
}

body {
  margin: 0;
